/**
 * reducers.jsreducer
 * 
    
     API 
     Date.now()  Math.random()
 */
import { combineReducers } from 'redux'
import * as ActionTypes from "./actionTypes"
import localStorage from "@/util/storage";         //localStorage

/**
 * reducerstateactionstate
 */

//
const initialState = {
        showStatus: false,  //
        song: localStorage.getCurrentSong() || "",  //           localStorage
        songs: localStorage.getSongs() || "",  //
        loadStatus: false
    };

//Reducer
//
function showStatus(showStatus = initialState.showStatus, action) {
    switch (action.type) {
        case ActionTypes.SHOW_PLAYER:
            return action.showStatus;
        default:
            return showStatus;
    }
}

//audio
function loadStatus(loadStatus = initialState.loadStatus, action) {
    switch (action.type) {
        case ActionTypes.AUDIO_LOAD:
            return action.loadStatus;
        default:
            return loadStatus;
    }
}

//
function song(song = initialState.song, action) {
    switch (action.type) {
        case ActionTypes.CHANGE_SONG:
            localStorage.setCurrentSong(action.song);    //localStorage
            return action.song;
        default:
            return song;
    }
}
//
function songs(songs = initialState.songs, action) {
    switch (action.type) {
        case ActionTypes.SET_SONGS:
            localStorage.setSongs(action.songs);   ////localStorage
            return action.songs;
        case ActionTypes.REMOVE_SONG_FROM_LIST:
            let newSongs = songs.filter(song => song.id !== action.id);
            localStorage.setSongs(newSongs);
            return newSongs;
        default:
            return songs;
    }
}
//Reducer 
const reducer = combineReducers({
    showStatus,
    song,  
    songs,
    loadStatus 
}); 

export default reducer





